import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, FormControl, Validators, AbstractControl} from '@angular/forms';
import { Router } from '@angular/router';
import {
  ApiService,
  AuthService
   } from '../../services';
import { LoaderComponent } from '../shared';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  entryComponents: [ LoaderComponent ]
})
export class LoginComponent implements OnInit {
  public user: FormGroup;
  public usrnameInvalid: boolean;
  public pwordInvalid: boolean;
  public loading: boolean = false;
  public auth;
  public router;
  public loaderOptions;
  public serverError: boolean = false;
  public serverErrorMsg: string = 'username/password is incorrect';

  constructor (_auth: AuthService, _router: Router) {
    this.usrnameInvalid = false;
    this.pwordInvalid = false;
    this.router = _router;
    this.auth =  _auth;
    this.loaderOptions = {
      width: '30px',
      height: '30px'
    };
  }

  onSubmit() {
    if (this.credentialsIsCorrect()) {
      this.login();
    }
    return false;
  }

  credentialsIsCorrect(): boolean {
    this.usrnameInvalid = this.user.get('username').value.trim() === '';
    this.pwordInvalid = this.user.get('password').value.trim() === '';

    this.serverError = false;

    return !this.usrnameInvalid && !this.pwordInvalid;
  }

  ngOnInit() {
    this.user = new FormGroup({
      username: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required])
    });
  }

  clearErrors(specificError) {
    switch (specificError) {
      case 'username':
        this.usrnameInvalid = false;
        break;
      case 'password':
        this.pwordInvalid = false;
        break;
      default:
        break;
    }
  }

  login() {
    const user = {
      username: this.user.get('username').value,
      password: this.user.get('password').value
    };
    this.loading = true;

    this.auth.login(user, (err, data) => {
      if (err) {
        this.serverError = true;
        this.loading = false;
      } else {
        this.loading = false;
        if (data.roleId === 5) {
          this.router.navigate(['/dashboard']);
        } else if (data.roleId === 4) {
          this.router.navigate(['/admindashboard']);
        } else {
          this.auth.logout();
          this.router.navigate(['/pageNotFound']);
        }
      }
    });
  }
}



