import { Injectable } from '@angular/core';
import { AuthService } from '../auth/auth.service';
import { DataService } from '../store-service/shared-data.service';
import { Router, ActivatedRoute, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

@Injectable()
export class IsLoggedInGuard implements CanActivate {

  constructor(private router: Router, private auth: AuthService) { }
  canActivate() {
    if (!this.auth.isLoggedIn()) {
      this.router.navigate(['login']);
      return false;
    } else {
      return true;
    }
  }
}

@Injectable()
export class IsLoggedOutGuard implements CanActivate {
  constructor(private router: Router, private auth: AuthService) { }
  canActivate() {
    if (this.auth.isLoggedIn()) {
      this.router.navigate(['dashboard']);
      return false;
    } else {
      return true;
    }
  }
}

@Injectable()
export class CustomerExistsGuard implements CanActivate {
  public state;
  public snapshot;
  constructor(private store: DataService, private router: Router, private auth: AuthService) {
      this.state = router.routerState;
      this.snapshot = this.state.snapshot;
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    let customerId = this.getIdFromUrl(state.url);
    if (this.customerExists(customerId)) {
      return true;
    }

    this.router.navigate(['login']);
    return false;
  }

  getIdFromUrl(url) {
    return Number(url.replace('/form/', ''));
  }

  customerExists(customerId) {
    const customers = this.store.getData('customers');
    for (let i = 0; i < customers.length; i++) {
      if (customers[i].customerId === customerId) {
        return true;
      }
    }

    return false;
  }
}



