import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { routes } from '../../constant';
import { DataService } from '../store-service/shared-data.service';
import { ApiService } from '../api/api.service';
import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';

@Injectable()
export class AuthService {
  public store;
  public router;
  public api;

  constructor(_store: DataService, _router: Router, _api: ApiService, private idle: Idle) {
    this.store = _store;
    this.router = _router;
    this.api = _api;
   }

  login (credentials, cb) {
    this.api.request(routes.LOGIN_URL, 'POST', credentials).subscribe(data => {
      this.store.keepData('userId', data.userId.toString());
      this.store.keepData('username', `${data.firstName} ${data.lastName}`);
      sessionStorage.setItem('alat-token', data.token);
      cb(null, data);
    }, err => {
      cb(err);
    });
  }

  logout () {
    this.store.clearData();
    sessionStorage.removeItem('alat-token');
    sessionStorage.removeItem('alat-auth-timer');
    sessionStorage.removeItem('alat-auth-time');
    this.router.navigate(['/']);
  }

  isLoggedIn() {
    return this.store.getData('userId') !== false ? true : false;
  }

  currentUser() {
    return this.store.getData('userId');
  }

  getUserName() {
    return this.store.getData('username');
  }

  watchForInActivity(inactivityTimeout) {
    this.idle.setIdle(5);
    this.idle.setTimeout(inactivityTimeout);
    this.idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);
    this.idle.onIdleEnd.subscribe(() => console.log('No longer this.idle.'));
    this.idle.onIdleStart.subscribe(() => console.log('You\'ve gone idle!'));
    this.idle.onTimeout.subscribe(() => {
      this.logout();
    });
    this.idle.watch();
  }
}



