import { Component, OnInit, AfterViewChecked, ViewChildren } from '@angular/core';
import { ApiService, AuthService , DataService, EventEmitterService } from '../../services';
import { routes } from '../../constant';
import { Router, ActivatedRoute } from '@angular/router';
import { ModalComponent } from '../shared';
import { CreateuserComponent } from '../createuser/createuser.component';

@Component({
  selector: 'app-field-officers',
  templateUrl: './all-fieldofficers.component.html',
  styleUrls: ['./all-fieldofficers.component.scss'],
  entryComponents: [ ModalComponent, CreateuserComponent ]
})
export class AllFieldOfficersComponent implements OnInit {
  @ViewChildren('cs') fieldOfficers;
  public loading = true;
  public sFilter = '';
  public fOfficers: Object[];
  public previouslyOpenedDropdown;
  public visible = false;
  public enabled: boolean;
  public loggedinUser: Number;
  public modalCreate = false;
  public modalReassign = false;
  public errorModalOptions;
  public foId;
  public createModalType;
  public currentFieldOfficer = null;
  public modal = false;
  public showAssign = false;
  public currentFO = null;
  public customers;
  public foStatus;
  public title;
  public assignOptions;
  public reassignOptions;
  public allfieldOfficers = [];
  public loadingScreenOptions;
  public states = ['aa'];
  public cities = [];

  constructor(private api: ApiService, private router: Router, private auth: AuthService, private route: ActivatedRoute,
  private em: EventEmitterService, private store: DataService) {
    this.loadingScreenOptions = {text: 'loading...'};
    this.fetchStatus();
    api.request(routes.GET_FO_URL + this.auth.currentUser(), 'GET', {}).subscribe(data => {
      this.allfieldOfficers = data;
      this.loading = false;
      this.store.keepData('fieldofficers', data);
      this.sortFieldOfficers(data);
      this.updateStates();
    }, err => {
      this.loading = false;
      this.fOfficers = [];
      this.loading = false;
      this.updateStates();
    });

    this.assignOptions = {
      customers: [],
      submit: null
    };
    this.reassignOptions = {
      fo_list: [],
      submit: null
    };

    this.loggedinUser = Number(auth.currentUser());
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
    };
    this.createModalType = 'create';
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Server Error',
      message: ''
    };
  }

  updateStates() {
    this.api.request(routes.STATES_URL, 'GET').subscribe(data => {
      this.states = data;
      this.triggerMaterializeSelectFields();
    });
  }

  triggerMaterializeSelectFields() {
    $(document).ready(() => {
      $('.select').material_select('destroy');
      $('.select').material_select();
      $('select#state').on('change', ($event) => {
        let stateId = $($event.target).val();
        if (stateId) {
          this.updateCity(stateId);
        }
      });
      $('select#city').on('change', ($event) => {
        let cityId = $($event.target).val();
        if (cityId) {
          this.fOfficers = this.updateFieldOfficersList(cityId);
        } else {
          this.sortFieldOfficers(this.allfieldOfficers);
        }
      });
    });
  }

  updateFieldOfficersList(cityId) {
    let newList = [];
    this.allfieldOfficers.forEach(fo => {
      if (fo.citys.length) {
        fo.citys.forEach(f => {
          if (f.id === +cityId ) {
            newList.push(fo);
            return;
          }
        });
      }
    });

    return newList;
  }

  updateCity(stateId) {
    this.api.request(`${routes.CITIES}?stateId=${stateId}`, 'GET').subscribe(data => {
      this.cities = data;
      $(document).ready(() => {
        $('.select').material_select('destroy');
        $('.select').material_select();
      });
    });
  }

  ngOnInit() {
    switch (this.foStatus) {
      case 'all':
        this.title = 'All Field Officers';
        break;
      case 'unavailable':
        this.title = 'Unavailable Field Officers';
        break;
      case 'available':
        this.title = 'Available Field Officers';
        break;
      default:
        this.title = 'Field Officers';
    }
  }

  showModal(display: string, type, fieldOfficer) {
    this.modal = (display === 'show');
    this.createModalType = type;
    this.currentFieldOfficer = fieldOfficer;
    this.em.emitter$.emit(type);
  };

  assign(id) {
    this.loading = true;
    this.api.request(routes.UNASSIGNED_CUSTOMERS, 'POST', {
      fieldOfficerId: id,
      loggedUser: +this.auth.currentUser()
    }).subscribe(data => {
      this.loading = false;
      this.assignOptions.customers = data;
      this.configureAssignOptions(id);
    }, err => {
      this.loading = false;
      this.errorModalOptions.message = 'Could not load customers';
      this.errorModalOptions.hide = false;
    });
  }

  configureAssignOptions(id) {
    this.assignOptions.submit = (selectedCustomers, cb) => {
      let payload = {
        loggedOnUser: this.auth.currentUser(),
        fieldOfficerTo: id,
        customerId: []
      };
      selectedCustomers.forEach(customer => {
        payload.customerId.push(customer.customerId);
      });
      this.api.request(routes.FO_ASSIGN, 'POST', payload).subscribe(data => {
        this.refreshList();
        cb(null, data);
      }, err => {
        cb(err);
      });
    };
    this.currentFO = id;
    this.showAssign = true;
  }

  hideModal(display: string) {
    this.modal = (display === 'hide');
  };

  pageBodyClicked($e) {
    let target = $($e.target);
    if (!target.hasClass('dropdown') && this.previouslyOpenedDropdown) {
      this.previouslyOpenedDropdown.hide();
    }
  }

  hideModalReassign(display: string) {
    if (display === 'refresh') {
      this.refreshList();
    }
    this.modalReassign = (display === 'hide');
  };

  openDropdown(id, customer) {
    let enabledCircle;
    let dropdown;
    let wrapper;

    if (customer) {
      enabledCircle = $(customer).find('.fa-circle');
      if (enabledCircle.hasClass('bd-green')) {
        this.enabled = true;
      } else if (enabledCircle.hasClass('bd-red')) {
        this.enabled = false;
      }
      dropdown = $(customer).find('.ddcontent');
      wrapper = $(customer).find('.ctx-menu');
      if (this.previouslyOpenedDropdown && this.previouslyOpenedDropdown.attr('id') !== dropdown.attr('id')) {
          this.previouslyOpenedDropdown.hide();
      }
      wrapper.css('position', 'relative');
      dropdown.css({position: 'absolute', 'z-index': 70});
      dropdown.slideToggle();
      this.previouslyOpenedDropdown = dropdown;
    }
  }

  openFOPage(id) {
    this.router.navigate(['fieldofficer', id || 11]);
  }

  disable(id, customer) {
    let enabledCircle;
    if (customer) {
      $(customer).parent().addClass('-grey');
      this.api.request(routes.DISABLE_URL, 'POST', {
        officerTDisable: id,
        loggedUser: this.loggedinUser
      }).subscribe(data => {
        enabledCircle = $(customer).find('.fa-circle');
        enabledCircle.removeClass('bd-green');
        enabledCircle.addClass('bd-red');
        this.errorModalOptions.hide = false;
        this.errorModalOptions.title = 'Success';
        this.errorModalOptions.message = 'User disabled successfully';
        $(customer).parent().removeClass('-grey');

        if (this.foStatus === 'available') {
          this.fOfficers.forEach((data: any, i) => {
            if (data.fieldOfficerId === id) {
              this.fOfficers.splice(i, 1);
              return;
            }
          });
        }
      }, err => {
         this.errorModalOptions.hide = false;
         this.errorModalOptions.title = 'Failed';
         this.errorModalOptions.message = 'Failed to disable user';
         $(customer).parent().removeClass('-grey');
      });
    }
  }

  enable(id, customer) {
    let disabledCircle;
    if (customer) {
      $(customer).parent().addClass('-grey');
      this.api.request(routes.ENABLE_URL, 'POST', {
        officerToEnable: id,
        loggedUser: this.loggedinUser
      }).subscribe(data => {
        disabledCircle = $(customer).find('.fa-circle');
        disabledCircle.removeClass('bd-red');
        disabledCircle.addClass('bd-green');
        this.errorModalOptions.hide = false;
        this.errorModalOptions.title = 'Success';
        this.errorModalOptions.message = 'User enabled successfully';
        $(customer).parent().removeClass('-grey');

        if (this.foStatus === 'unavailable') {
          this.fOfficers.forEach((data: any, i) => {
            if (data.fieldOfficerId === id) {
              this.fOfficers.splice(i, 1);
              return;
            }
          });
        }
      }, err => {
        this.errorModalOptions.hide = false;
        this.errorModalOptions.title = 'Failed';
        this.errorModalOptions.message = 'Failed to enable user';
        $(customer).parent().removeClass('-grey');
      });
    }
  }

  wrapperClicked(data) {
    this.showAssign = false;
  }

  refreshList() {
    this.api.request(routes.GET_FO_URL + this.auth.currentUser(), 'GET', {}).subscribe(data => {
      this.fOfficers = this.getEnabledCustomers(data);
    }, err => {
      this.fOfficers = [];
    });
  }

  getEnabledCustomers(customers) {
    return customers.filter(cs => cs.isEnable);
  }

  fetchStatus() {
    let sub = this.route.params.subscribe(params => {
      let status = params['filter'];
      this.foStatus = status;
    });
  }

  sortFieldOfficers(data) {
    let availablecount  = [];
    let unavailablecount = [];
      for (let i = 0; i < data.length; i++) {
        if (data[i].isEnable === true ) {
          availablecount.push(data[i]);
        } else if (data[i].isEnable === false) {
          unavailablecount.push(data[i]);
        }
      }
      if (this.foStatus === 'all') {
        this.fOfficers = data;
      } else if (this.foStatus === 'available') {
        this.fOfficers = availablecount;
      } else if (this.foStatus === 'unavailable') {
        this.fOfficers = unavailablecount;
      } else {
        this.router.navigate(['pageNotFound']);
      }
  }

  getAssignmentId(foId, cb) {
    let assignmentIds;
    let requestPayload = {fieldOfficerId: foId, loggedUser: +this.auth.currentUser()};
    this.api.request(routes.FO_CUSTOMERS, 'POST', requestPayload).subscribe(data => {
      assignmentIds = data.map(r => r.id);
      cb(assignmentIds);
    });
  }

  dummyData() {
    return [
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'George James', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Frank Dunnelly ', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Kenny James', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Bolade Yemisi', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Tunde Kelani', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Thomas Friday', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Shakira Pique', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Shawn Davies', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Tutu Sade', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Timothy Haruna', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Kim Kelly', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Dumebi Joseph', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Joseph Dumal Fredrick', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'James Shina', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Tola Coker', totalCustomer: 32, enable: false},
      { userId: 1, firstName: 'Funmi Badmus', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32, enable: true},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32},
      { userId: 1, firstName: 'Bolade Fredrick', totalCustomer: 32}
    ];
  }
}



