let crypto = require('crypto-js');
import { Injectable } from '@angular/core';
import { SystemConstant } from '../../constant';

@Injectable()
export class DataService {
  private dataHolder: any = {};
  private persistedData: Object = {
    userId: '__ss0001',
    customers: '__ss0002',
    fieldofficers: '__ss0003',
    unassignedCustomers: '__ss0004',
    username: '__ss0005'
  };
  constructor() {}

  keepData(key, sharedData): void {
    if (Object.keys(this.persistedData).indexOf(key) !== -1) {
      this.persistData(this.persistedData[key], this.encryptData(sharedData));
    }

    this.dataHolder[key] = sharedData;
  }

  getData(key) {
    if (Object.keys(this.persistedData).indexOf(key) !== -1) {
      return this.getPersistedData(this.persistedData[key]) ?
        this.decryptData(this.getPersistedData(this.persistedData[key])) : false;
    }

    return this.dataHolder[key];
  }

  removeData(key) {
    if (Object.keys(this.persistedData).indexOf(key) !== -1) {
      sessionStorage.removeItem(this.persistedData[key]);
      return;
    }
    delete this.dataHolder[key];
  }

  encryptData(unEncryptedData) {
    let data = typeof unEncryptedData === 'object' ? JSON.stringify(unEncryptedData) : unEncryptedData;
    return crypto.AES.encrypt(data, SystemConstant.ENCRYPTIONSECERTKEY).toString();
  }

  decryptData(encryptedData) {
    let result;
    let decryptedData = crypto.AES.decrypt(encryptedData.toString(), SystemConstant.ENCRYPTIONSECERTKEY).toString(crypto.enc.Utf8);
    try {
      result = JSON.parse(decryptedData);
    } catch (e) {
      result = decryptedData;
    }
    return result;
  }

  persistData(key, encryptedNumber) {
    sessionStorage.setItem(key, encryptedNumber);
  }

  getPersistedData(key) {
    return sessionStorage.getItem(key);
  }

  clearData() {
    Object.keys(this.persistedData).forEach(data => this.removeData(data));
    this.dataHolder = {};
  }
}



