import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormArray, FormControl, AbstractControl, FormGroup } from '@angular/forms';
import { ModalComponent } from '../shared';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService, ApiService, AuthService } from '../../services';
import { routes } from '../../constant';

@Component({
  selector: 'app-fieldofficerdashboard',
  templateUrl: './fieldofficerdashboard.component.html',
  styleUrls: ['./fieldofficerdashboard.component.scss'],
  entryComponents: [ ModalComponent ]
})

export class FieldOfficerDashboardComponent implements OnInit, OnDestroy {
  public customerList;
  public checkAll = false;
  public my_form;
  public formControls;
  public selectedCustomers = [];
  public selectedIds = [];
  public noofCustomers;
  public modal = false;
  public random;
  public errorModalOptions;
  public sFilter = '';
  public sub;
  public foId;
  public showAssign = false;
  public foDetails: any = {};
  public customers;
  public selectedCustomerIds;
  public fetched = false;
  public assignOptions;
  public reassignOptions;
  public allfieldOfficers;
  public selectedCity;
  public allCustomers;
  public cities = [];
  public states = null;
  public loadingScreen = false;

  constructor(private router: Router, private auth: AuthService,
  private api: ApiService, private route: ActivatedRoute, private store: DataService) {
    let formControls;
    this.customerList = [];
    this.fetchId();
    this.foDetails = this.getFoDetails(this.foId);
    formControls = this.getControls();
    this.my_form = new FormGroup(formControls);
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Please select a customer.'
    };
    this.api.request(routes.UNASSIGNED_CUSTOMERS, 'POST', {
      fieldOfficerId: this.foId,
      loggedUser: +this.auth.currentUser()
    }).subscribe(data => {
      this.assignOptions.customers = data;
      this.customers = data;
    });

    this.assignOptions = {
      customers: [],
      submit: null
    };

    this.reassignOptions = {
      fo_list: [],
      submit: null
    };

    let requestPayload = {fieldOfficerId: this.foId, loggedUser: +this.auth.currentUser()};
    this.api.request(routes.FO_CUSTOMERS, 'POST', requestPayload).subscribe(data => {
      this.allCustomers = data;
      this.customerList = this.allCustomers;
      formControls = this.getControls();
      this.my_form = new FormGroup(formControls);
      this.fetched = true;
    });

    this.api.request(routes.STATES_URL, 'GET').subscribe(data => {
      this.states = data;
      this.triggerMaterializeSelectFields();
    });
  };

  triggerMaterializeSelectFields() {
    $(document).ready(() => {
      $('.select').material_select('destroy');
      $('.select').material_select();
      $('select#state').on('change', ($event) => {
        let stateId = $($event.target).val();
        if (stateId) {
          this.updateCity(stateId);
        }
      });
      $('select#city').on('change', ($event) => {
        let cityId = $($event.target).val();
        if (cityId) {
          this.selectedCity = cityId;
          this.reloadList(cityId);
        } else {
          this.selectedCity = null;
          this.customerList = [];
        }
      });
    });
  }

  updateCity(stateId) {
    this.api.request(`${routes.CITIES}?stateId=${stateId}`, 'GET').subscribe(data => {
      this.cities = data;
      $(document).ready(() => {
        $('.select').material_select('destroy');
        $('.select').material_select();
      });
    });
  }

  reloadList(cityId) {
    this.customerList = this.allCustomers.filter(cs => cs.customerCityId === +cityId);
  }

  showModal(display: string, id = null, cityId=null) {
    if (!id && this.getSelectedCustomers().length === 0) {
      this.errorModalOptions.message = 'Please select a customer.';
      this.errorModalOptions.hide = false;
    } else {
      this.openFOModal(display, id, cityId);
    }
  };

  openFOModal(display, id, cityId=null) {
    console.log('aaa');
    this.loadingScreen = true;
    this.getFObyCity(cityId, () => {
      this.loadingScreen = false;
      this.selectedCustomerIds = id ? [id] : this.getSelectedIds();
      this.modal = (display === 'show');
      this.reassignOptions.fo_list = this.getEnabledCustomers(this.allfieldOfficers).filter(cs => cs.fieldOfficerId !== this.foId);
      this.reassignOptions.submit = ((selectedFO, cb) => {
        this.getAssignmentId(this.selectedCustomerIds, this.foId, (assignmentIds => {
          let reassignparams;
          reassignparams = {
            loggedOnUser: this.auth.currentUser(),
            fieldOfficerTo: selectedFO.fieldOfficerId,
            fieldOfficerFrom: this.foId,
            assignmentId: assignmentIds
          };
          cb(null, '1');
          this.api.request(routes.REASSIGN_URL, 'POST', reassignparams).subscribe(data => {
            this.refreshList();
            cb(null, data);
          }, err => {
            cb(err);
          });
        }));
      });
    });
  }

  getFObyCity(cityId, cb) {
    console.log('selected CIty', this.selectedCity, cityId);
    let payload = {
      currentUserId: +this.auth.currentUser(),
      city: cityId || this.selectedCity
    };
    this.api.request(routes.GET_FO_BY_CITY, 'POST', payload).subscribe(data => {
      this.allfieldOfficers = data;
      cb();
    }, err => {
      this.allfieldOfficers = [];
      cb();
    });
  }

  showModalAssign() {
    if (this.getSelectedCustomers().length !== 0) {
      this.errorModalOptions.hide = false;
      this.errorModalOptions.message = 'Selected customer(s) have already been assigned to this field officer';
    } else {
      this.assignOptions.submit = (selectedCustomers, cb) => {
        let payload = {
          loggedOnUser: this.auth.currentUser(),
          fieldOfficerTo: this.foId,
          customerId: []
        };
        selectedCustomers.forEach(customer => {
          payload.customerId.push(customer.customerId);
        });
        this.api.request(routes.FO_ASSIGN, 'POST', payload).subscribe(data => {
          this.refreshList();
          cb(null, data);
        }, err => {
          cb(err);
        });
      };
      this.showAssign = true;
    }
  };

  getEnabledCustomers(customers) {
    return customers.filter(cs => cs.isEnable);
  }

  hideModal(display: string) {
    this.modal = (display === 'hide');
  };

  wrapperClicked(display: string) {
    this.showAssign = false;
  };

  gotoDashboard() {
    this.router.navigate(['/admin-dashboard']);
  };

  refreshList() {
    let formControls;
    let requestPayload = {fieldOfficerId: this.foId, loggedUser: +this.auth.currentUser()};
    this.api.request(routes.FO_CUSTOMERS, 'POST', requestPayload).subscribe(data => {
      this.allCustomers = data;
      this.customerList = this.allCustomers;
      formControls = this.getControls();
      this.my_form = new FormGroup(formControls);
      this.fetched = true;
    });
  }

  getAssignmentId(selectedIds, foId, cb) {
    let assignmentIds;
    let requestPayload = {fieldOfficerId: foId, loggedUser: +this.auth.currentUser()};
    this.api.request(routes.FO_CUSTOMERS, 'POST', requestPayload).subscribe(data => {
      assignmentIds = data.filter(d => {
        if (selectedIds.indexOf(d.customerId) !== -1) {
          return d;
        }
      }).map(r => r.id);
      cb(assignmentIds);
    });
  }

  getControls(dft = null) {
    let controls = {};
    if (dft) {
      controls[`checkbox`] = new FormControl(false);
      return controls;
    }
    for (let i = 0; i < this.customerList.length; i++) {
      controls[`check-box-${i}`] = new FormControl(false);
    }
    return controls;
  };

  getSelectedCustomers() {
    this.selectedCustomers = [];
    this.customerList.forEach((customer, i) => {
      if (this.my_form.value[`check-box-${i}`]) {
        this.selectedCustomers.push(customer);
      }
    });
    return this.selectedCustomers;
  };

  goBack() {
    history.back();
  }

  getSelectedIds() {
    this.selectedIds = [];
    this.customerList.forEach((customer, i) => {
      if (this.my_form.value[`check-box-${i}`]) {
        this.selectedIds.push(customer.customerId);
      }
    });
    return this.selectedIds;
  };

  toggleAll() {
    this.checkAll = !this.checkAll;
    for (let i = 0; i < this.foDetails.assignmentCount; i++) {
      this.my_form.controls[`check-box-${i}`].setValue(this.checkAll);
    }
  };

  getFoDetails(id) {
    let fieldofficers = this.store.getData('fieldofficers');
    for (let i = 0; i < fieldofficers.length; i++) {
      if (fieldofficers[i].fieldOfficerId === id) {
        return fieldofficers[i];
      }
    }
  };

  getFoReport(id) {
    this.router.navigate(['fieldreport', id]);
  }

  ngOnInit() {};

  fetchId() {
    this.sub =  this.route.params.subscribe(params => {
      let id = +params['id'];
      this.foId = id;
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  };

  dummyData() {
    return [
      { id: 12, name: 'Bolade Fredrick', account: '3427717015', dateAss: '1/1/10'},
      { id: 1, name: 'George James', account: '3427717001', dateAss: '1/2/10'},
      { id: 10, name: 'Frank Dunnelly ', account: '3427717002', dateAss: '2/2/10'},
      { id: 9, name: 'Kenny James', account: '3427717003', dateAss: '2/4/16'},
      {  id: 11, name: 'Bolade Yemisi', account: '33427717004', dateAss: '3/6/09'},
      {  id: 2, name: 'Tunde Kelani', account: '3427717005', dateAss: '5/10/15'},
      {  id: 3, name: 'Thomas Friday', account: '3427717006', dateAss: '12/11/17'},
      {  id: 121, name: 'Shakira Pique', account: '3427717007', dateAss: '1/5/10'},
      { id: 32, name: 'Shawn Davies', account: '3427717008', dateAss: '2/4/12'},
      {  id: 42, name: 'Tutu Sade', account: '3427717009', dateAss: '3/1/12'},
      {  id: 52, name: 'Timothy Haruna', account: '3427717010', dateAss: '2/4/02'}
    ];
  }
}



