import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormArray, FormControl, AbstractControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService, AuthService } from '../../services';
import { routes } from '../../constant';

@Component({
  selector: 'app-all-customers',
  templateUrl: './all-customers.component.html',
  styleUrls: [
    './all-customers.component.scss',
    '../fieldofficerdashboard/fieldofficerdashboard.component.scss'
  ]
})
export class AllCustomersComponent implements OnInit {
  public customerList;
  public checkAll = false;
  public myForm;
  public loading = true;
  public selectedIds = [];
  public errorModalOptions;
  public modal = false;
  public sFilter = '';
  public states = null;
  public cities = [];
  public selectedCity = null;
  public assignOptions = {
    fo_list: [],
    submit: null
  };
  public showLoadingScreen = false;

  constructor(private router: Router, private auth: AuthService, private api: ApiService) {
    let formControls;

    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Please select a customer.'
    };

    this.api.request(`${routes.ALL_UNASSIGNED_CUSTOMERS + auth.currentUser()}` , 'GET').subscribe(data => {
      this.loading = false;
      this.customerList = data;
      formControls = this.getControls();
      this.myForm = new FormGroup(formControls);

      this.api.request(routes.STATES_URL, 'GET').subscribe(data => {
        this.states = data;
        this.triggerMaterializeSelectFields();
      });
    },
    err => {
      this.loading = false;
      this.errorModalOptions.hide = false;
      this.errorModalOptions.title = 'Server Occured';
      this.errorModalOptions.message = 'Could not load customers, please refresh page';
      this.customerList = [];
      formControls = this.getControls();
      this.myForm = new FormGroup(formControls);
    });

    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Please select a customer.'
    };

    api.request(routes.GET_FO_URL + this.auth.currentUser(), 'GET', {}).subscribe(data => {
      this.assignOptions.fo_list = data;
    });
  }

  triggerMaterializeSelectFields() {
    $(document).ready(() => {
      $('.select').material_select('destroy');
      $('.select').material_select();
      $('select#state').on('change', ($event) => {
        let stateId = $($event.target).val();
        if (stateId) {
          this.updateCity(stateId);
        }
      });
      $('select#city').on('change', ($event) => {
        let cityId = $($event.target).val();
        if (cityId) {
          this.selectedCity = cityId;
          this.reloadList(cityId);
        } else {
          this.selectedCity = null;
          this.customerList = [];
        }
      });
    });
  }

  showModalAssign(id = null, cityId = null) {
    let payload = {
      currentUserId: +this.auth.currentUser(),
      city: cityId || this.selectedCity
    };
    this.showLoadingScreen = true;

    if (!id) {
      this.getSelectedIds();
      if (this.selectedIds.length === 0) {
        this.showLoadingScreen = false;
        this.errorModalOptions.hide = false;
        this.errorModalOptions.title = 'Error';
        this.errorModalOptions.message = 'Please select a customer';
        return;
      }
    }

    this.api.request(routes.GET_FO_BY_CITY, 'POST', payload).subscribe(data => {
      this.showLoadingScreen = false;
      this.assignOptions.fo_list = data;
      if (id) {
        this.selectedIds = [id];
      } else {
        this.getSelectedIds();
      }

      this.initializeSubmitCallback();
      this.modal = true;
    }, err => {
      this.showLoadingScreen = false;
      this.errorModalOptions.hide = false;
      this.errorModalOptions.title = 'Server Error';
      this.errorModalOptions.message = 'failed to fetch field officers';
    });
  };

  initializeSubmitCallback() {
    this.assignOptions.submit = (selectedFO, cb) => {
      let payload = {
        loggedOnUser: this.auth.currentUser(),
        fieldOfficerTo: selectedFO.fieldOfficerId,
        customerId: this.selectedIds
      };
      this.api.request(routes.FO_ASSIGN, 'POST', payload).subscribe(data => {
        this.refreshList();
        cb(null, data);
      }, err => {
        cb(err);
      });
    };
  }

  getControls() {
    let controls = {};
    this.customerList.forEach((_, i) => {
      controls[`checkbox-${i}`] = new FormControl(false);
    });
    return controls;
  };

  ngOnInit() {}

  refreshList() {
    let formControls;
    this.api.request(`${routes.ALL_UNASSIGNED_CUSTOMERS + this.auth.currentUser()}` , 'GET').subscribe(data => {
      this.customerList = data;
      formControls = this.getControls();
      this.myForm = new FormGroup(formControls);
    });
  }

  hideModal(display: string) {
    this.modal = (display === 'hide');
  };

  getSelectedIds() {
    this.selectedIds = [];
    this.customerList.forEach((customer, i) => {
      if (this.myForm.value[`checkbox-${i}`]) {
        this.selectedIds.push(customer.customerId);
      }
    });
  };

  toggleAll() {
    this.checkAll = !this.checkAll;
    this.customerList.forEach((_, i) => {
      this.myForm.controls[`checkbox-${i}`].setValue(this.checkAll);
    });
  };

  updateCity(stateId) {
    this.api.request(`${routes.CITIES}?stateId=${stateId}`, 'GET').subscribe(data => {
      this.cities = data;
      $(document).ready(() => {
        $('.select').material_select('destroy');
        $('.select').material_select();
      });
    });
  }

  reloadList(cityId) {
    let payload = {
      currentUserId: +this.auth.currentUser(),
      city: cityId
    };
    this.api.request(routes.GET_UNASSIGNED_CUSTOMERS_BY_CITY, 'POST', payload).subscribe(data => {
      this.customerList = data;
    });
  }

  dummyData() {
    return [
      { id: 12, firstName: 'Bolade Fredrick', lastName: '', customerId: 1, account: '3427717015', customerCity: '1/1/10'},
      { id: 1, firstName: 'George James', lastName: '', customerId: 2, account: '3427717001', customerCity: '1/2/10'},
      { id: 10, firstName: 'Frank Dunnelly ', lastName: '', customerId: 3, account: '3427717002', customerCity: '2/2/10'},
      { id: 9, firstName: 'Kenny James', lastName: '', customerId: 4, account: '3427717003', customerCity: '2/4/16'},
      {  id: 11, firstName: 'Bolade Yemisi', lastName: '', customerId: 5, account: '33427717004', customerCity: '3/6/09'},
      {  id: 2, firstName: 'Tunde Kelani', lastName: '', customerId: 6, account: '3427717005', customerCity: '5/10/15'},
      {  id: 3, firstName: 'Thomas Friday', lastName: '', customerId: 7, account: '3427717006', customerCity: '12/11/17'},
      {  id: 121, firstName: 'Shakira Pique', lastName: '', customerId: 8, account: '3427717007', customerCity: '1/5/10'},
      { id: 32, firstName: 'Shawn Davies', lastName: '', customerId: 9, account: '3427717008', customerCity: '2/4/12'},
      {  id: 42, firstName: 'Tutu Sade', lastName: '', customerId: 10, account: '3427717009', customerCity: '3/1/12'},
      {  id: 52, firstName: 'Timothy Haruna', lastName: '', customerId: 11, account: '3427717010', customerCity: '2/4/02'}
    ];
  }
}



