import { Input, Component, OnInit, Output, EventEmitter } from '@angular/core';
import {FormBuilder, FormGroup, FormControl, Validators, AbstractControl} from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService, DataService, EventEmitterService, AuthService } from '../../services';
import { LoaderComponent, ModalComponent } from '../shared';
import { SystemConstant, routes } from '../../constant';

@Component({
  selector: 'app-createuser',
  templateUrl: './createuser.component.html',
  styleUrls: ['./createuser.component.scss'],
  entryComponents: [ LoaderComponent, ModalComponent ]
})
export class CreateuserComponent implements OnInit {
  @Output() wrapperClicked = new EventEmitter();
  @Input() type = 'create';
  @Input() fieldOfficer: any = {};

  public newuser: FormGroup;
  public firstnameInvalid = false;
  public lastnameInvalid = false;
  public statesInvalid = false;
  public emailInvalid = false;
  public mobileInvalid = false;
  public cityInvalid = false;
  public states;
  public cities = [];
  public errorModalOptions;
  public modalOptions;
  public loaderOptions = {};

  public loading = false;
  public serverError = false;
  public emailErrorText: string;
  public invalidEmail = false;
  public modalContent;




  constructor(private api: ApiService, private store: DataService, private router: Router,
  private em: EventEmitterService, private auth: AuthService) {
    em.emitter$.subscribe(item => {
      this.newuser.get('firstname').setValue('');
      this.newuser.get('lastname').setValue('');
      this.newuser.get('email').setValue('');
      this.newuser.get('mobile').setValue('');
      this.clearErrors('all');

      if (item === 'update') {
        setTimeout(() => {
          this.newuser.get('firstname').setValue(this.fieldOfficer.firstName || '');
          this.newuser.get('lastname').setValue(this.fieldOfficer.lastName || '');
          this.newuser.get('email').setValue(this.fieldOfficer.email || '');
          this.newuser.get('mobile').setValue(this.fieldOfficer.mobileNumber || '');
        }, 200);
      }
    });
    this.firstnameInvalid = false;
    this.lastnameInvalid  = false;
    this.statesInvalid = false;
    this.emailInvalid = false;
    this.mobileInvalid = false;
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Failed to create User'
    };
     this.modalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'success',
      hide: true,
      icon: false,
      title: 'Confirm Submission',
      message: 'Are you sure you want to submit, click the Submit button to proceed',
      button: {
        text: 'Submit',
        action: () => {
          this.create();
        }
      }
    };
    this.loaderOptions = {
      width: '30px',
      height: '30px',
      text: 'Fetching Customers...'
    };
  }

   credentialsIsCorrect(): boolean {
    this.firstnameInvalid = this.newuser.get('firstname').value.trim()  === '';
    this.lastnameInvalid = this.newuser.get('lastname').value.trim() === '';
    this.statesInvalid = this.newuser.get('states').value.trim() === '';
    this.emailInvalid = this.newuser.get('email').value.trim() === '' || !SystemConstant.EMAILREGEX.test(this.newuser.get('email').value);
    this.mobileInvalid = this.newuser.get('mobile').value.trim() === '';
    this.serverError = false;

    return !this.firstnameInvalid && !this.lastnameInvalid && !this.statesInvalid && !this.emailInvalid && !this.mobileInvalid;
  }

  clickWrapper($e) {
    if ($($e.target).hasClass('modal-wrapper') || $($e.target).hasClass('close')) {
      this.wrapperClicked.emit('clicked');
    }
  }

  onSubmit() {
    if (this.credentialsIsCorrect()) {
      this.modalOptions.hide = false;
    } else {
      this.modalOptions.hide = true;
      return false;
    }
  }

  stateSelected(stateId) {
    this.api.request(`${routes.CITIES}?stateId=${stateId}`, 'GET').subscribe(data => {
      this.cities = data;
      $(document).ready(function() {
        $('.myselect').material_select('destroy');
        $('.myselect').material_select();
      });
    });
  }

  getSelectedCities() {
    let unparsedData = $('select.myselect').val();
    console.log('cal', unparsedData, 'selec');
    let cityIds = [];
    unparsedData.forEach(data => {
      cityIds.push(+data.split(':')[1].trim().replace(new RegExp('\'', 'g'), ''));
    });
    return cityIds;
  }

  ngOnInit() {
    $(document).ready(function() {
      $('.myselect').material_select('destroy');
      $('.myselect').material_select();
    });
    this.newuser = new FormGroup({
      firstname: new FormControl('', [Validators.required]),
      lastname: new FormControl('', [Validators.required]),
      states: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required]),
      mobile: new FormControl('', [Validators.required]),
      city: new FormControl('', [Validators.required])
    });
    this.api.request(routes.STATES_URL, 'GET').subscribe(data => {
      this.states = data;
    });
  }

  clearErrors(specificError) {
    switch (specificError) {
      case 'firstname':
        this.firstnameInvalid = false;
        break;
      case 'lastname':
        this.lastnameInvalid = false;
        break;
      case 'states':
        this.statesInvalid = false;
        break;
      case 'email':
        this.emailInvalid = false;
        break;
      case 'city':
        this.cityInvalid = false;
        break;
      case 'mobile':
        this.mobileInvalid = false;
        break;
      case 'all':
        this.firstnameInvalid = false;
        this.lastnameInvalid = false;
        this.statesInvalid = false;
        this.emailInvalid = false;
        this.mobileInvalid = false;
        break;
      default:
        break;
    }
  }

  create() {
    const route = this.type === 'create' ? routes.CREATE_URL : routes.UPDATE_URL;
    let newuser: any = {
      firstname: this.newuser.get('firstname').value,
      lastname: this.newuser.get('lastname').value,
      EmailAddress: this.newuser.get('email').value,
      username: this.newuser.get('email').value,
      mobileNumber: this.newuser.get('mobile').value,
      city: this.getSelectedCities(),
      createdBy: +this.auth.currentUser()
    };
    if (this.type !== 'create') {
      newuser.userId = this.fieldOfficer.fieldOfficerId;
    }
     this.modalOptions.hide = true;
     this.loading = true;
     this.api.request(route, 'POST', newuser).subscribe(data => {
      this.loading = false;
      this.errorModalOptions.hide = false;
      this.errorModalOptions.title = 'Success';
      this.errorModalOptions.message = this.type === 'create' ? 'User created successfully' : 'User updated successfully';
      location.reload();
    }, err => {
      this.loading = false;
      this.modalOptions.hide = true;
      this.errorModalOptions.message = this.type === 'create' ? 'Failed to create User' : 'Failed to update User';
      this.errorModalOptions.hide = false;
    });
  };
}





