import { Component, OnInit, OnDestroy } from '@angular/core';
import {FormBuilder, FormGroup, FormControl, Validators, AbstractControl} from '@angular/forms';
import { ApiService, DataService, AuthService, GeolocationService } from '../../services';
import { routes } from '../../constant';
import { Router, ActivatedRoute } from '@angular/router';
import { ModalComponent } from '../shared';

@Component({
  selector: 'app-kycform',
  templateUrl: './kycform.component.html',
  styleUrls: ['./kycform.component.scss'],
  entryComponents: [ ModalComponent ]
})
export class KycFormComponent implements OnInit, OnDestroy {
  public customer: FormGroup;
  public sub;
  public customerDetails;
  public previewLink;
  public passport;
  public modalOptions;
  public errorModalOptions;
  public loading = false;
  public addressInvalid = false;
  public presentorabsentInvalid = false;
  public customerRepInvalid = false;
  public buildingDetailsInvalid = false;
  public landmarkInvalid = false;
  public commentsInvalid = false;
  public photoInvalid = false;
  public relationInvalid = false;
  public picTakenAt: string;
  public picTaken = false;
  public retake = false;
  public submitWarning = false;
  public serverError = false;
  public serverErrorMsg = 'Please fill all required fields';
  public customerId;
  public fileInput;
  public loadingOptions;
  public assignmentId;
  public customerAvatar;

  constructor(private auth: AuthService,
  private route: ActivatedRoute, private store: DataService, private api: ApiService,
  private router: Router, private geo: GeolocationService) {
    this.modalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'success',
      hide: true,
      icon: false,
      title: 'Confirm Submission',
      message: 'Are you sure you want to submit, click the Submit button to proceed',
      button: {
        text: 'Submit',
        action: () => {
          this.sendData();
        }
      }
    };

    this.loadingOptions = {text: 'submitting...'};
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'A server error occured, please try again.'
    };
    auth.watchForInActivity(300);
  };

  ngOnInit() {
    this.geo.getLocation((err, location) => {
      if (err) {
        this.picTakenAt = 'Location is not available';
      }
      this.picTakenAt = location;
    });
    this.sub = this.route.params.subscribe(params => {
    let id = +params['id'];
    this.customerId = id;
    this.customerDetails = this.getCustomerDetails(id);
    this.assignmentId = this.getAssignmentId(id);
    this.customer = new FormGroup({
      customername: new FormControl('', [Validators.required]),
      address: new FormControl('', [Validators.required]),
      presentorabsent: new FormControl('', [Validators.required]),
      customerrep: new FormControl('', [Validators.required]),
      buildingdetails: new FormControl('', [Validators.required]),
      landmark: new FormControl('', [Validators.required]),
      comments: new FormControl('', [Validators.required]),
      relation: new FormControl('', [Validators.required]),
      img: new FormControl('')
    });
    console.log('aa', this.customerDetails);
    this.api.request(routes.CUSTOMER_AVATAR + this.customerDetails.imageUrl,
      'GET', null, null, true, true).subscribe(data => {
      this.customerAvatar = data.url;
      });
    });
  }

  getAssignmentId(id) {
    let aId;
    this.store.getData('customers').forEach(c => {
      if (c.customerId === id) {
        aId = c.fieldOfficerAssignmentId;
        return;
      }
    });
    return aId;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  getCustomerDetails(id) {
    let customers = this.store.getData('customers');
    for (let i = 0; i < customers.length; i++) {
      if (customers[i].customerId === id) {
        return customers[i];
      }
    }

    return null;
  }

  fileChange(input) {
    let reader = new FileReader();
    this.fileInput = input;
    this.readFile(input.files[0], reader, (result) => {
      if (input.files[0].size > 100000) {
        let img = document.createElement('img');
        img.src = result;
        this.resize(img, 250, 250, (resized_image) => {
          this.previewLink = resized_image;
          this.passport = resized_image;
        });
      } else {
        this.previewLink = result;
        this.passport = result;
      }
    });
    this.picTaken = true;
  };

  readFile(file, reader, cb) {
    reader.onload = () => {
      cb(reader.result);
    };
    reader.readAsDataURL(file);
  }

  resize(img, MAX_WIDTH: number, MAX_HEIGHT: number, callback) {
  // This will wait until the img is loaded before calling this function
    return img.onload = () => {

      // Get the images current width and height
      let width = img.width;
      let height = img.height;

      // Set the WxH to fit the Max values (but maintain proportions)
      if (width > height) {
          if (width > MAX_WIDTH) {
              height *= MAX_WIDTH / width;
              width = MAX_WIDTH;
          }
      } else {
          if (height > MAX_HEIGHT) {
              width *= MAX_HEIGHT / height;
              height = MAX_HEIGHT;
          }
      }

      // create a canvas object
      let canvas = document.createElement('canvas');

      // Set the canvas to the new calculated dimensions
      canvas.width = width;
      canvas.height = height;
      let ctx = canvas.getContext('2d');

      ctx.drawImage(img, 0, 0,  width, height);
      let dataUrl = canvas.toDataURL('image/jpeg');

      callback(dataUrl, img.src.length, dataUrl.length);
    };

  };

  credentialsIsCorrect(): boolean {
    this.addressInvalid =  (this.customer.get('address').dirty && this.customer.get('address').invalid)
    || (this.customer.get('address').untouched && this.customer.get('address').invalid);

    this.presentorabsentInvalid =  (this.customer.get('presentorabsent').dirty && this.customer.get('presentorabsent').invalid)
    || (this.customer.get('presentorabsent').untouched && this.customer.get('presentorabsent').invalid);

    if (this.customer.get('presentorabsent').value === 'no') {
      this.customerRepInvalid = this.customer.get('customerrep').value.trim() === '';

      this.relationInvalid =  (this.customer.get('relation').dirty && this.customer.get('relation').invalid)
      || (this.customer.get('relation').untouched && this.customer.get('relation').invalid);
    }

    this.buildingDetailsInvalid = this.customer.get('buildingdetails').value.trim() === '';
    this.landmarkInvalid = this.customer.get('landmark').value.trim() === '';
    this.commentsInvalid = this.customer.get('comments').value.trim() === '';
    this.photoInvalid = this.passport === undefined;

    this.serverError = false;

    return !this.addressInvalid && !this.customerRepInvalid && !this.buildingDetailsInvalid && !this.landmarkInvalid
      && !this.commentsInvalid && !this.photoInvalid && !this.relationInvalid;
  }

  onSubmit() {
    if (this.credentialsIsCorrect()) {
      this.modalOptions.hide = false;
    }
    return false;
  }

  clearErrors(error) {
    switch (error) {
      case 'address':
        this.addressInvalid = false;
        break;
      case 'presentorabsent':
        this.presentorabsentInvalid = false;
        break;
      case 'customerrep':
        this.customerRepInvalid = false;
        break;
      case 'relation':
        this.relationInvalid = false;
        break;
      case 'buildingdetails':
        this.buildingDetailsInvalid = false;
        break;
      case 'landmark':
        this.landmarkInvalid = false;
        break;
      case 'comments':
        this.commentsInvalid = false;
        break;
      case 'img':
        this.photoInvalid = false;
        break;
      default:
        break;
    }
  }

  sendData() {
    let cs: FormData = this.getFormData();
    this.modalOptions.hide = true;
    this.loading = true;
    this.api.request(routes.SUBMIT_URL, 'POST', cs, {}, false).subscribe(data => {
      this.store.keepData('customer-details', data);
      this.loading = false;
      this.router.navigate(['/dashboard']);
    }, err => {
      this.serverError = true;
      this.loading = false;
      this.modalOptions.hide = true;
      this.errorModalOptions.hide = false;
    });
  }

  getFormData() {
    let cs = new FormData();
    cs.append('CustomerId', this.customerId);
    cs.append('LocationAddressSameAsDocument', this.customer.get('address').value === 'yes');
    cs.append('CustomerPresent', this.customer.get('presentorabsent').value === 'yes');
    cs.append('NameofPersonMet', this.customer.get('customerrep').value);
    cs.append('BuildingDescription', this.customer.get('buildingdetails').value);
    cs.append('Landmarks', this.customer.get('landmark').value);
    cs.append('Comments', this.customer.get('comments').value);
    cs.append('File', this.canvasToFile(this.passport), this.fileInput.files[0].name);
    cs.append('PersonMetRelationshipToCustomer', this.customer.get('relation').value);
    cs.append('FieldOfficerAssignmentId', this.assignmentId);

    return cs;
  }

  canvasToFile(dataURL) {
    let blobBin = atob(dataURL.split(',')[1]);
    let array = [];
    for (let i = 0; i < blobBin.length; i++) {
        array.push(blobBin.charCodeAt(i));
    }
    let file = new Blob([new Uint8Array(array)], {type: 'image/png'});

    return file;
  }
};



