import { RouterModule, Routes } from '@angular/router';
import {
    LoginComponent,
    BlankComponent,
    LandingPageComponent,
    KycFormComponent,
    CreateuserComponent,
    AllFieldOfficersComponent,
    FieldOfficerDashboardComponent,
    AllCustomersComponent,
    DashboardComponent,
    FieldreportComponent,
    RejectReportComponent
} from './components/';

import { IsLoggedInGuard, CustomerExistsGuard, IsLoggedOutGuard } from './services/';
import {
  HeaderComponent,
  LoadingScreenComponent,
  ErrorpageuserComponent,
  ErrorpageadminComponent
} from './components/shared';

const routes: Routes = [
  { path: '', component: LoginComponent, canActivate: [IsLoggedOutGuard] },
  { path: 'login', component: LoginComponent, canActivate: [IsLoggedOutGuard] },
  { path: 'form/:id', component: KycFormComponent },
  { path: 'dashboard', component: LandingPageComponent },
  { path: 'admin', component: ErrorpageadminComponent },
  { path: 'allfieldofficers/:filter', component: AllFieldOfficersComponent },
  { path: 'pageNotFound', component: ErrorpageadminComponent },
  { path: 'allcustomers', component: AllCustomersComponent },
  { path: 'showcase', component: BlankComponent },
  { path: 'pageNotFound', component: ErrorpageadminComponent },
  { path: 'fieldofficer/:id', component: FieldOfficerDashboardComponent},
  { path: 'admindashboard', component: DashboardComponent},
  { path: 'fieldreport/:id', component: FieldreportComponent},
  { path: 'reject', component: RejectReportComponent },
  { path: '**', component: ErrorpageuserComponent },
];

export const routing = RouterModule.forRoot(routes);




