import { Injectable } from '@angular/core';
import { ApiService } from '../api/api.service';
import { routes } from '../../constant';

@Injectable()
export class GeolocationService {
  public imglocationlat;
  public imglocationlong;

  setPosition(position) {
    this.imglocationlat = position.coords.latitude;
    this.imglocationlong = position.coords.longitude;
  };

  constructor(private api: ApiService) {}

  getLocation(callback) {
    if(navigator.geolocation){
      navigator.geolocation.getCurrentPosition((p) => {
        this.setPosition(p);
        this.api.request(routes.GMAP_URL + '?latlng=' + this.imglocationlat + "," + this.imglocationlong, 'GET', {}, {'Accept': 'application/json'}).subscribe(data => {
          callback(null, data.results[0].formatted_address);
        }, err => {
          callback(err);
       });
      });
    };
  }

}



