/**
 * This is a reusable tooltip component class. It is used to add tooltips that point
 *      point any element of your choice
 * 
 * Usage: Wrap the tooltip element and the element it references in a div. The tooltip
 *      element has to be below the element its referencing
 */
import { Component, Input, AfterViewInit } from '@angular/core';

@Component({
    selector: 'app-tooltip',
    templateUrl: './tooltip.component.html',
    styleUrls: ['./tooltip.component.scss']
})
export class ToolTipComponent implements AfterViewInit {
    @Input() elementRef: string;
    @Input() position: string;
    @Input() tooltipWidth;
    public tooltipClass: string;
    public tooltip: any;
    public tooltipParent: any;
    public tooltipRef: any;
    public xPos: any;
    public yPos: any;

    constructor() {
        let randomStr = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 5);
        this.tooltipClass = 'tooltip-' + randomStr;
    }

    ngAfterViewInit() {
        $(document).ready(() => {
            this.tooltip = $('.' + this.tooltipClass).parent();
            this.tooltipRef = $(this.elementRef);
            this.tooltipParent = this.tooltipRef.parent();

            // make position of parent relative
            this.tooltipParent.css('position', 'relative');
            this.tooltip.css('position', 'absolute');

            // calculate horizontal position for tooltip
            if (this.position === 'right') {
                this.xPos = this.tooltipRef.width() + parseInt(this.tooltipRef.css('margin-left'), 10);
            } else {
                this.xPos = 0 - this.tooltip.width() + parseInt(this.tooltipRef.css('margin-left'), 10);
            }

            // calculate vertical position for tooltip
            this.yPos = 0;
            if (this.tooltip.height() < this.tooltipRef.height()) {
                this.yPos = ((this.tooltipRef.height() - this.tooltip.height()) / 2) + parseInt(this.tooltipRef.css('margin-top'), 10);
            }
            this.tooltip.css('left', this.xPos);
            this.tooltip.css('top', this.yPos);
        });
    }
}



