import { Component, OnInit, Input, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService, ApiService, DataService } from '../../services';
import { routes } from '../../constant';
import { LoaderComponent, ModalComponent } from '../shared';

@Component({
  selector: 'app-fieldreport',
  templateUrl: './fieldreport.component.html',
  styleUrls: ['./fieldreport.component.scss'],
  entryComponents: [ LoaderComponent, ModalComponent ]
})
export class FieldreportComponent implements OnInit {
  public allCustomers = [];
  public customer;
  public loading = false;
  public loadingbutton = false;
  public loaderOptions;
  public errorModalOptions;
  public modalOptions;
  public selectedCustomerVerificationId = null;
  public selectedFieldOfficerId = null;
  public modal = false;
  public createModalType;
  public buildingImage;
  public foStatus;
  public showFo;
  public foId;
  public fo;
  public imageViewerOptions: any = {show: false};
  public relationshipList;
  public relationshipStatus;

  constructor(private router: Router, private auth: AuthService, private api: ApiService, private route: ActivatedRoute,
  private store: DataService) {
    this.fetchStatus();
    this.fetchId();
    this.fo = this.getFoDetails(this.foId);
    this.loading = true;
    this.api.request(`${routes.ALL_UNVERIFIED_CUSTOMERS}loggedUserId=${auth.currentUser()}&statusId=1`, 'GET').subscribe(data => {
      this.loading = false;
      this.sortReports(data);
      if (this.allCustomers.length) {
        this.customer = this.allCustomers[0];
        this.selectedCustomerVerificationId = this.customer.customerAddressVerificationId;
        this.selectedFieldOfficerId = this.customer.fieldOfficerId;
        this.api.request(routes.BUILDINGIMAGE_URL + this.customer.imageName,
          'GET', null, null, true, true).subscribe(data => {
          this.buildingImage = data.url;
          this.imageViewerOptions.url = data.url;
        });
      }
    }, err => {
      this.loading = false;
      this.sortReports([]);
    });

    this.loaderOptions = {
      width: '30px',
      height: '30px'
    };

    this.modalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'success',
      hide: true,
      icon: false,
      title: 'Confirm Report',
      button: {
        text: 'Confirm and Submit',
        action: () => {
          this.acceptReport();
        }
      }
    };

    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Failed to Accept Report'
    };
    this.createModalType = 'reject';
   }

   getRelationshipStatus() {
     this.relationshipList = this.relationshipwithCustomer();
     this.relationshipList.forEach((data: any, i) => {
          if (this.relationshipList[i].id.toString() === this.customer.personMetRelationshipToCustomer) {
            this.relationshipStatus = this.relationshipList[i].value;
          }
      });
   }

   showCustomerReport(id) {
     for (let i = 0; i < this.allCustomers.length; i++) {
      if (this.allCustomers[i].customerId === id) {
        this.customer = this.allCustomers[i];
        this.selectedCustomerVerificationId = this.customer.customerAddressVerificationId;
        this.selectedFieldOfficerId = this.customer.fieldOfficerId;
        break;
      }
     }
     return this.customer;
   }

   showModal(display: string, type, cid = null, fid = null) {
     this.createModalType = type;
     this.selectedCustomerVerificationId = cid;
     this.selectedFieldOfficerId = fid;
     this.modal = (display === 'show');
   }

   wrapperClicked(display: string) {
    this.modal = (display === 'hide');
   }

   acceptReport() {
     const payload = {
       currentUserId: +this.auth.currentUser(),
       customerAddressVerificationId: this.customer.customerAddressVerificationId,
     };
     this.loadingbutton = true;
     this.api.request(routes.ACCEPT_REPORT_URL, 'POST', payload).subscribe(data => {
       this.modalOptions.hide = true;
       this.loadingbutton = false;
        this.errorModalOptions.hide = false;
        this.errorModalOptions.message = 'Report Accepted Successfully';
        this.errorModalOptions.title = 'Success';
        this.allCustomers.forEach((data: any, i) => {
          if(data.customerId === this.customer.customerId) {
            this.allCustomers.splice(i, 1);
            this.customer = this.allCustomers[0];
          }
        });
     }, err => {
      this.modalOptions.hide = true;
      this.loadingbutton = false;
      this.errorModalOptions.hide = false;
      this.errorModalOptions.message = 'Failed to accept Report';
      this.errorModalOptions.title = 'Failed';
     });
   }

   openAcceptModal() {
     this.modalOptions.hide = false;
   }

   failedToRejectOrFail(event) {
     this.modal = false;
     this.errorModalOptions.hide = false;
     this.errorModalOptions.title = 'Failure';
     this.errorModalOptions.message = event.type === 'reject' ? 'Failed to reject Report' : 'Failed to fail Report';
   }

   removeCustomer(event) {
    this.modal = false;
    this.errorModalOptions.hide = false;
    this.errorModalOptions.title = 'Success';
    this.errorModalOptions.message = event.type === 'reject' ? 'Report rejected' : 'Report failed';
    this.allCustomers.forEach((data: any, i) => {
      if (data.customerAddressVerificationId === event.cvid) {
        this.allCustomers.splice(i, 1);
        this.customer = this.allCustomers[0];
      }
    });
  };

  fetchStatus() {
    let sub = this.route.params.subscribe(params => {
      let status = params['id'];
      this.foStatus = status;
    });
  }

  sortReports(data) {
    if (this.foStatus === 'all') {
      this.allCustomers = data;
      this.setInfo(this.allCustomers);
    } else if (this.foId) {
      this.api.request(routes.FO_REPORTS + 'loggedUserId=' + this.auth.currentUser() + '&' +
      'fieldOfficerID=' + this.foId + '&' + 'statusId=' + 1 , 'GET').subscribe(data => {
        this.allCustomers = data;
        this.setInfo(this.allCustomers);
        this.showFo = true;
      });
    } else {
      this.router.navigate(['pageNotFound']);
    }
  }

  setInfo(data) {
    if (data.length) {
      this.customer = data[0];
      this.selectedCustomerVerificationId = this.customer.customerAddressVerificationId;
      this.selectedFieldOfficerId = this.customer.fieldOfficerId;
      this.getRelationshipStatus();
      this.api.request(routes.BUILDINGIMAGE_URL + this.customer.imageName,
        'GET', null, null, true, true).subscribe(data => {
        this.buildingImage = data.url;
      });
    }
  }

  fetchId() {
    let subId =  this.route.params.subscribe(params => {
      let id = +params['id'];
      this.foId = id;
    });
  }

  getFoDetails(id) {
    let fieldofficers = this.store.getData('fieldofficers');
    for (let i = 0; i < fieldofficers.length; i++) {
      if (fieldofficers[i].fieldOfficerId === id) {
        return fieldofficers[i];
      }
    }
  };

  openImageViewer(url) {
    this.imageViewerOptions.show = true;
    this.imageViewerOptions.url = url;
  }

  ngOnInit() {
  }

  dummyData() {
    return [
      { id: 12, customerName: 'Bolade Fredrick', account: '3427717015', customerCity: '1/1/10', customerId: 12, fieldOfficerId: 1},
      { id: 1, customerName: 'George James', account: '3427717001', customerCity: '1/2/10', customerId: 10, fieldOfficerId: 8},
      { id: 10, customerName: 'Frank Dunnelly ', account: '3427717002', customerCity: '2/2/10', customerId: 22, fieldOfficerId: 99},
      { id: 9, customerName: 'Kenny James', account: '3427717003', customerCity: '2/4/16'},
      {  id: 11, customerName: 'Bolade Yemisi', account: '33427717004', customerCity: '3/6/09'},
      {  id: 2, customerName: 'Tunde Kelani', account: '3427717005', customerCity: '5/10/15'},
      {  id: 3, customerName: 'Thomas Friday', account: '3427717006', customerCity: '12/11/17'},
      {  id: 121, customerName: 'Shakira Pique', account: '3427717007', customerCity: '1/5/10'},
      {   id: 32, customerName: 'Shawn Davies', account: '3427717008', customerCity: '2/4/12'},
      {  id: 42, customerName: 'Tutu Sade', account: '3427717009', customerCity: '3/1/12'},
      {  id: 52, customerName: 'Timothy Haruna', account: '3427717010', customerCity: '2/4/02'}
    ];
  }

  relationshipwithCustomer() {
    return [
      { id: 1, value: 'Mother'},
      { id: 2, value: 'Father'},
      { id: 3, value: 'Brother'},
      { id: 4, value: 'Sister'},
      { id: 5, value: 'Cousin'},
      { id: 6, value: 'Nephew'},
      { id: 7, value: 'Niece'},
      { id: 8, value: 'Aunty'},
      { id: 9, value: 'Uncle'},
      { id: 10, value: 'Grandmother'},
      { id: 11, value: 'Grandfather'},
      { id: 12, value: 'Friend'},
      { id: 13, value: 'Colleague'},
      { id: 14, value: 'Other'}
    ]
  }

}



