import { Component, AfterContentChecked, Input } from '@angular/core';
import { ErrorOptions } from './error.interface';

@Component({
  selector: 'app-error',
  templateUrl: 'error.component.html',
  styleUrls: ['error.component.scss']
})
export class ErrorComponent implements AfterContentChecked {
  @Input() options: ErrorOptions;
  public errorData: any = {};

  ngAfterContentChecked() {
    this.errorData.width = this.options.width;
    this.errorData.message = this.options.message;
  }
}



