import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators, AbstractControl} from '@angular/forms';
import { ApiService, AuthService } from '../../services';
import { ModalComponent, LoaderComponent } from '../shared';
import { routes } from '../../constant';

@Component({
  selector: 'app-reject-report',
  templateUrl: './reject-report.component.html',
  styleUrls: ['./reject-report.component.scss'],
  entryComponents: [ ModalComponent ]
})
export class RejectReportComponent implements OnInit {
  @Output() wrapperClicked = new EventEmitter();
  @Output() reportHandled = new EventEmitter();
  @Output() failedToHandleReport = new EventEmitter();
  @Input() type = 'reject';
  @Input() customerAddressVerificationId;
  @Input() foId;
  public errorModalOptions;
  public loaderOptions;
  public validateReport: FormGroup;
  public loading = false;
  public commentInvalid = false;
  public modal = true;

  constructor(private api: ApiService, private auth: AuthService) {
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Failed to Reject Report'
    };

    this.loaderOptions = {
      width: '30px',
      height: '30px',
      text: 'Fetching Customers...'
    };

    this.modal = true;
  }

  ngOnInit() {
    this.validateReport = new FormGroup({
      comment: new FormControl('', [Validators.required])
    });
  }

  credentialsIsCorrect(): boolean {
    this.commentInvalid = this.validateReport.get('comment').value.trim()  === '';
    return !this.commentInvalid;
  }

  clickWrapper($e) {
    if ($($e.target).hasClass('modal-wrapper')) {
      this.errorModalOptions.hide = true;
      this.wrapperClicked.emit('clicked');
    }
  }

  hidemodal() {
    console.log('hiding me');
    this.modal = false;
  }

  onSubmit() {
    if (this.credentialsIsCorrect()) {
      this.reject();
    }
  }

  reject() {
    const route = this.type === 'reject' ? routes.REJECT_REPORT_URL : routes.FAILED_REPORT_URL;
    let reqparams: any = {
      currentUserId: +this.auth.currentUser(),
      customerAddressVerificationId: this.customerAddressVerificationId
    };
    if (this.type !== 'reject') {
      reqparams.failedComment =  this.validateReport.get('comment').value;
    } else {
      reqparams.rejectComment = this.validateReport.get('comment').value
    }
    this.loading = true;
    this.api.request(route, 'POST', reqparams).subscribe(data => {
      console.log('should not be here');
      this.loading = false;
      this.hidemodal();
      this.reportHandled.emit({cvid: this.customerAddressVerificationId, type: this.type});
    }, err => {
      console.log('got here on failure');
      this.loading = false;
      this.hidemodal();
      this. failedToHandleReport.emit({cvid: this.customerAddressVerificationId, type: this.type});
      console.log('got here on failure');
      this.errorModalOptions.hide = false;
      this.errorModalOptions.message = this.type === 'reject' ? 'Failed to reject report' : 'Failed to fail report';
    });
  }

}



