import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { FormBuilder, FormArray, FormControl, AbstractControl, FormGroup } from '@angular/forms';
import { AuthService, ApiService, DataService} from '../../services';
import { routes } from '../../constant';
import { LoaderComponent, ModalComponent } from '../shared';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-fo-list-modal',
  templateUrl: './fo-list-modal.component.html',
  styleUrls: ['./fo-list-modal.component.scss']
})
export class FoListModalComponent implements OnInit {

@Output() wrapperClicked = new EventEmitter();
  @Input() options;
  public officerList;
  public myForm;
  public selectedFieldOfficer;
  public loading = false;
  public loadingOptions;
  public errorModalOptions;
  public modalOptions;

  constructor(private api: ApiService, private store: DataService,
  private auth: AuthService, private route: ActivatedRoute, private router: Router) {
  }

  ngOnInit() {
    let formControls;
    if (this.options) {
      this.officerList = this.options.fo_list || [];
    } else {
      this.officerList = [];
    }

    formControls = this.getControls();
    this.myForm = new FormGroup(formControls);
    this.errorModalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'error',
      hide: true,
      icon: false,
      title: 'Failed',
      message: 'Please select a field officer.'
    };
    this.modalOptions = {
      overlayColor: 'rgba(0,0,0,0.3)',
      type: 'success',
      hide: true,
      icon: false,
      title: 'Confirm Submission',
      message: 'Are you sure you want to submit, click the Submit button to proceed',
      button: {
        text: 'Submit',
        action: () => {
          this.loading = true;
          this.modalOptions.hide = true;
          this.options.submit(this.selectedFieldOfficer, (err, data) => {
            this.loading = false;
            if (err) {
              this.errorModalOptions.message = 'A server error occured';
              this.errorModalOptions.title = 'Failed';
              this.errorModalOptions.hide = false;
            } else {
              this.errorModalOptions.title = 'Success';
              this.errorModalOptions.message = 'Request Successful.';
              this.errorModalOptions.hide = false;
              setTimeout(() => { this.wrapperClicked.emit('clicked'); }, 1000);
            }
          });
        }
      }
    };
  }

  getControls() {
    let controls = {};
    this.officerList.forEach((officer, index) => {
      controls[`radio-${index}`] = new FormControl(false);
    });
    return controls;
  }

  clickWrapper($e) {
    if ($($e.target).hasClass('modal-wrapper') || $($e.target).hasClass('close')) {
      this.wrapperClicked.emit('clicked');
    }
  }

  getSelectedFieldOfficer() {
    this.officerList.forEach((officer, i) => {
      if (this.myForm.value[`radio-${i}`]) {
        this.selectedFieldOfficer = officer;
      }
    });
    return this.selectedFieldOfficer;
  }

  onSubmit() {
    if (this.getSelectedFieldOfficer() === undefined) {
      this.errorModalOptions.title = 'Failed';
      this.errorModalOptions.message = 'Please select a field officer';
      this.errorModalOptions.hide = false;
    } else {
      this.modalOptions.message = 'Are you sure you want to submit, click the Submit button to proceed';
      this.modalOptions.hide = false;
    }
  }

  dummyData() {
    return [
      {firstName: 'Bolade Fredrick', assignmentCount: '20'},
      {firstName: 'George James', assignmentCount: '13'},
      {firstName: 'Frank Dunnelly ', assignmentCount: '24'},
      {firstName: 'Kenny James', assignmentCount: '27'},
      {firstName: 'Bolade Yemisi', assignmentCount: '40'},
      {firstName: 'Tunde Kelani', assignmentCount: '50'},
      {firstName: 'Thomas Friday', assignmentCount: '70'},
      {firstName: 'Shakira Pique', assignmentCount: '80'},
      {firstName: 'Shawn Davies', assignmentCount: '40'},
      {firstName: 'Tutu Sade', assignmentCount: '20'},
      {firstName: 'Timothy Haruna', assignmentCount: '10'}
    ];
  }
}



